import React, { Component } from 'react'
import Layout from "../components/layout";
import ImgCustom from "../components/img-custom";
import {postsData} from "../../data/tmc.js";
import SEO from '../components/seo';

class GameTmc extends Component {
    constructor(props) {
        super(props);
        this.state = {
            type_1: '',
            type_2: '',
            melee_1: '',
            melee_2: '',
            posts: postsData,
            currentPost: {},
        };

        this.handleChange = this.handleChange.bind(this);
    }

    choicePost(post){
        this.setState({currentPost: post});
    }

    filterPost(type, typeValue){
        this.setState({currentPost: {}});
        let newArray = postsData
        let that = this
        let type1Value = type == 'type_1' ? typeValue : that.state.type_1
        let type2Value = type == 'type_2' ? typeValue : that.state.type_2
        let melee1Value = type == 'melee_1' ? typeValue : that.state.melee_1
        let melee2Value = type == 'melee_2' ? typeValue : that.state.melee_2

        if(type1Value){
            newArray = newArray.filter(function (el) {
                return el.type_1 == type1Value;
            });
        }
        if(type2Value){
            newArray = newArray.filter(function (el) {
                return el.type_2 == type2Value;
            });
        }
        if(melee1Value){
            newArray = newArray.filter(function (el) {
                return el.melee_1 == melee1Value;
            });
        }
        if(melee2Value){
            newArray = newArray.filter(function (el) {
                return el.melee_2 == melee2Value;
            });
        }

        this.setState({posts: newArray});
    }

    handleChange(event) {
        let type = event.target.id
        this.setState({[type]: event.target.value});
        this.filterPost(type, event.target.value)
    }

    render() {
        return (
            <Layout>
                <SEO />

                <div className="m-auto main">
                    <div className="bg-white my-2 p-4">
                        <div className="row">
                            <label>Phái:</label>
                            <div className="col-6 mb-4">

                                <select className="form-select" defaultValue="all" id="type_1" value={this.state.type_1 ? this.state.type_1 : " "} onChange={this.handleChange}>
                                    <option value=""></option>
                                    <option value="Tà Phái">Tà Phái</option>
                                    <option value="Chính Phái">Chính Phái</option>
                                    <option value="Trung Lập">Trung Lập</option>
                                </select>
                            </div>
                            <div className="col-6 mb-4">
                                <select className="form-select" defaultValue="all" id="type_2" value={this.state.type_2 ? this.state.type_2 : " "} onChange={this.handleChange}>
                                    <option value=""></option>
                                    <option value="Ngoại Công">Ngoại Công</option>
                                    <option value="Nội Công">Nội Công</option>
                                    <option value="Thân Pháp">Thân Pháp</option>
                                </select>
                            </div>
                        </div>

                        <div className="row">
                            <label>Tầm đánh:</label>
                            <div className="col-6 mb-4">

                                <select className="form-select" defaultValue="all" id="melee_1" value={this.state.melee_1 ? this.state.melee_1 : " "} onChange={this.handleChange}>
                                    <option value=""></option>
                                    <option value="Cận Chiến">Cận Chiến</option>
                                    <option value="Tầm Xa">Tầm Xa</option>
                                </select>
                            </div>
                            <div className="col-6 mb-4">
                                <select className="form-select" defaultValue="all" id="melee_2" value={this.state.melee_2 ? this.state.melee_2 : " "} onChange={this.handleChange}>
                                    <option value=""></option>
                                    <option value="Khống Chế">Khống Chế</option>
                                    <option value="Tấn Công">Tấn Công</option>
                                    <option value="Hỗ Trợ">Hỗ Trợ</option>
                                    <option value="Phòng Ngự">Phòng Ngự</option>
                                </select>
                            </div>
                        </div>

                        <hr/>

                        <div className="row">
                            <div className="col-3 skill_area">
                                {this.state.posts.map((node, index) => {
                                    return <div key={index} className={this.state.currentPost.name == node.name ? 'p-2 border-bottom hero-item active' : 'p-2 border-bottom hero-item'} onClick={() => this.choicePost(node)}>
                                        {node.name}
                                    </div>
                                })}
                            </div>
                            <div className="col-9 py-2">
                                <div className="d-flex">
                                    <div>
                                        <ImgCustom className="skill-thumb" imgName={this.state.currentPost.avatar} />
                                    </div>

                                    <div className="p-4">
                                        <div className="skill_description">
                                            {this.state.currentPost.description}
                                        </div>
                                        <hr/>
                                        <ImgCustom
                                            className="skill-thumb-test d-flex"
                                            type="skill_thumb"
                                            post={this.state.currentPost}

                                            imgName={this.state.currentPost.img_thumb_skill} />

                                        <hr/>

                                        <div className="text-center fw-bold">
                                            {this.state.currentPost.melee_1} - {this.state.currentPost.melee_2}
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Layout>
        );
    }

  }

export default GameTmc
