import React, { Component } from 'react'
import { graphql, StaticQuery } from 'gatsby'
import Img from "gatsby-image"


class ImgCustom extends Component {
    constructor(props) {
        super(props);
        this.state = {
            skillType: 'skill_1',
        };
    }

    choiceSkill(type){
        this.setState({skillType: type});
    }

    render() {
        return (

            <StaticQuery
                query={graphql`
                    query {
                        allImageSharp {
                            edges {
                            node {
                                fluid(maxWidth: 512, quality: 100) {
                                    aspectRatio
                                    base64
                                    originalName
                                    src
                                    srcSet
                                }
                            }
                            }
                        }
                    }
                `}

                render={data => {
                    const image = data.allImageSharp.edges.find(
                        edge => edge.node.fluid.originalName === this.props.imgName
                    )
                    const skill1 = this.props.post ? this.props.post.skill_1 : {}
                    const skill2 = this.props.post ? this.props.post.skill_2 : {}
                    const skill3 = this.props.post ? this.props.post.skill_3 : {}
                    const skill4 = this.props.post ? this.props.post.skill_4 : {}
                    if (!image) {
                        return null
                    }

                    if(this.props.type == 'skill_thumb'){
                        return <div>
                            <div className="d-flex mb-4 justify-content-around">
                                <div className={this.state.skillType == 'skill_1' ? "skill1 active" : "skill1"} style={{backgroundImage: 'url(' + image.node.fluid.src + ')' }} onClick={() => this.choiceSkill('skill_1')}></div>
                                <div className={this.state.skillType == 'skill_2' ? "skill2 active" : "skill2"} style={{backgroundImage: 'url(' + image.node.fluid.src + ')' }} onClick={() => this.choiceSkill('skill_2')}></div>
                                <div className={this.state.skillType == 'skill_3' ? "skill3 active" : "skill3"} style={{backgroundImage: 'url(' + image.node.fluid.src + ')' }} onClick={() => this.choiceSkill('skill_3')}></div>
                                <div className={this.state.skillType == 'skill_4' ? "skill4 active" : "skill4"} style={{backgroundImage: 'url(' + image.node.fluid.src + ')' }} onClick={() => this.choiceSkill('skill_4')}></div>
                            </div>
                            <div>
                                {this.state.skillType == 'skill_1' && skill1.data.map((node, index) => {
                                    return <div key={index} className={index === 0 ? 'skill_first' : 'skill_detail'}>
                                        {node}
                                    </div>
                                })}

                                {this.state.skillType == 'skill_2' && skill2.data.map((node, index) => {
                                    return <div key={index} className={index === 0 ? 'skill_first' : 'skill_detail'}>
                                        {node}
                                    </div>
                                })}

                                {this.state.skillType == 'skill_3' && skill3.data.map((node, index) => {
                                    return <div key={index} className={index === 0 ? 'skill_first' : 'skill_detail'}>
                                        {node}
                                    </div>
                                })}

                                {this.state.skillType == 'skill_4' && skill4.data.map((node, index) => {
                                    return <div key={index} className={index === 0 ? 'skill_first' : 'skill_detail'}>
                                        {node}
                                    </div>
                                })}

                            </div>

                        </div>
                    }
                    return <Img className={this.props.className ? this.props.className : ''} fluid={image.node.fluid} />
                }}
            />

        );
    }

}

export default ImgCustom
